*{margin:0px; padding:0px;}

ul,ol,li{
  list-style: none;
}

h2{
  text-align: center;
  padding:10px 0px;
}

.product{
  display: flex;
  width:1050px;
  margin:0px auto;
}

.product > li{
  width:25%;
  margin:10px 10px 0px 10px;
  font-size: 16px;
}

.product > li > p:first-child{
  overflow: hidden;
  display: block;
  line-height: 0px;
}

.product > li > p > img{
  width:100%;
  transition: 0.3s;
}

.product > li > p > img:hover{
  transform: scale(1.2);
}

.product > li > p:nth-child(2){
  width:100%;
  padding: 10px 0px;
  background-color: #fff;
}
.product > li > p:nth-child(3){
  font-weight: bold;
}

.product > li > p:last-child{
  font-size: 12px;
  color:#ccc;

}

